@mixin fontT($s: null, $l : null) {
	$size: null;
	$lh: null;
	@if $s == 13 {
		$lh : 20px;
	} @else if $s == 14 {
		$lh : 22px;
	} @else if $s == 15 {
		$lh : 23px;
	} @else if $s == 16 {
		$lh : 24px;
	} @else if $s == 17 {
		$lh : 25px;
	} @else if $s == 18 {
		$lh : 26px;
	} @else if $s == 20 {
		$lh : 30px;
	} @else {
		$lh : 1.5;
	}
	@if $l {
		$lh : $l;
	}
	font: {
		family: $font;
		weight: 400;
		size: $s + px;
	}
	line-height: $lh;
}
