	text-shadow: 0px 0px 1px rgba(0, 0, 0, 0.2);
	&::before, &::after {
		filter: drop-shadow(0px 0px 1px rgba(0, 0, 0, 0.2));
	}
}

/* Position */
@mixin Pos($p: null, $t: null, $r: null, $b: null, $l: null, $a: null, $z: null) {
	position: $p;
	z-index: $z;
	@if($a) {
		top: $a;
		right: $a;
		bottom: $a;
		left: $a;
	} @else {
		top: $t;
		right: $r;
		bottom: $b;
		left: $l;
	}
}

