@mixin Mix_Ellipsis01{
	white-space:nowrap;
	text-overflow:ellipsis;
	overflow:hidden;
}
@mixin Mix_Ellipsis($line){
	display:-webkit-box;
	text-overflow:ellipsis;
	overflow:hidden;
	-webkit-box-orient:vertical;
	-webkit-line-clamp:$line;
	// word-break:break-word;
	word-break: keep-all;
	overflow-wrap: break-word;
}